import { reducerToken, reducers } from './reducers';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EntityService, getReducer, Entity } from '.';

@NgModule({
  // declarations: [HelloWorldComponent],
  imports: [CommonModule],
  // exports: [HelloWorldComponent],
  // providers: [EntityService],
})
export class StoreGeneratorModule {
  // entities: Entity[] = []
  static forRoot(): ModuleWithProviders {
    // const reducerTemp: any = {};
    // for (let index = 0; index < entities.length; index++) {
    //   const { name: entityName } = entities[index];
    //   reducerTemp[entityName] = getReducer(entityName);
    // }
    return {
      ngModule: StoreGeneratorModule,
      providers: [
        {
          provide: reducerToken,
          useValue: reducers,
          // { Tenant: getReducer('Tenant'), User: getReducer('User') },
          // reducerTemp,
          // useValue: entities.reduce(
          //   (accumulator: any, entity: Entity) =>
          //     Object.assign(accumulator, { [entity.name]: getReducer(entity.name) }),
          //   {},
          // ),
        },
      ],
    };
  }
}



